<template>
<div>
  <h1>메인 화면</h1>
  <div id="app">
      <h2>firebase function 4</h2>
      <div class="demoView">
        <strong style="font-size: 20px;">결과 출력창  &nbsp; &nbsp; &nbsp; &nbsp;
          <input type="button" value="초기화" @click="testReset()">
          <br/><hr/><br/>
          <div id="result">{{this.data}}</div>
          <br/><br/>
        </strong>
      </div>

      <div class="container" style="border: 1px solid black; margin: 20px; padding: 20px">
        <textarea type="text" v-model="ckPswd" v-text="ckPswd"></textarea>
      </div>

      <div class="container" style="border: 1px solid black; margin: 20px; padding: 20px">
        <br>
        <button @click="callSimpleFunction()">변수X 함수</button>
        <button @click="callFunctionWithVariable()">변수 1개 함수</button>
      </div>

      <div class="container" style="border: 1px solid black; margin: 20px; padding: 20px">
        <div class="auth-section">
          <h2>Firebase Authentication 등록</h2>
          <input type="email" v-model="registerEmail" placeholder="Email">
          <input type="password" v-model="registerPassword" placeholder="Password">
          <button @click="register()">Register</button>
        </div>
        <div class="auth-section">
          <h2>Login</h2>
          <input type="email" v-model="loginEmail" placeholder="Email">
          <input type="password" v-model="loginPassword" placeholder="Password">
          <button @click="login()">Login</button>
        </div>
        <div class="auth-section">
          <h2>Text Analysis</h2>
          <textarea type="text" v-model="text" placeholder="Enter text to analyze"></textarea>
          <button @click="analyzeText()">텍스트 분석</button>
        </div>
      </div>
  </div>
  <h1>메인 화면</h1>
</div>

</template>

<style scoped>
.demoView{
  border: 1px solid black;
  background-color: gray;
  display: inline-block;
  width: 30%;
  z-index: 5;
  position: fixed;
  top: 200px;
  right: 10px;
}

</style>
<script>

export default {
  name: 'DefaultLayout',
    components: {
  },
  data() {
    return {
      name: "firebase userA",
      data: "초기",
      text: "",
      ckPswd: "pswd",
      registerEmail: "",
      registerPassword: "",
      loginEmail: "",
      loginPassword: "",
    };
  },
  created() {

  },
  methods: {
    testReset(){
      console.clear();
      this.data = "초기"
    },
    async callSimpleFunction(){
      if(this.ckPswd != process.env.VUE_APP_CKPSWD){
        return this.data = "권한없음"
      }
      this.data = "통신 중..."
      const signal = this.$netMngr.ctrller.add(this.scope)
      this.data = await this.$netMngr.firebase.callSimpleFunction(signal);
    },
    async callFunctionWithVariable(){
      if(this.ckPswd != process.env.VUE_APP_CKPSWD){
        return this.data = "권한없음"
      }
      this.data = "통신 중..."
      const signal = this.$netMngr.ctrller.add(this.scope)
      this.data = await this.$netMngr.firebase.callFunctionWithVariable(this.name ,signal);
    },
    async register() {
      try {
        await this.$netMngr.firebase.register(this.registerEmail, this.registerPassword);
        this.data = "User registered successfully.";
      } catch (error) {
        this.data = `Error registering user: ${error.message}`;
      }
    },
    async login() {
      try {
        await this.$netMngr.firebase.login(this.loginEmail, this.loginPassword);
        this.data = "User logged in successfully.";
      } catch (error) {
        this.data = `Error logging in user: ${error.message}`;
      }
    },   
    async analyzeText() {
      if(this.ckPswd !== process.env.VUE_APP_CKPSWD) {
        return this.data = "권한없음";
      }
      this.data = "분석 중...";
      this.data = await this.$netMngr.firebase.analyzeText(this.text);
    }


  },

  beforeUnmount() {
  //비동기 작업 종료 
  this.$netMngr.ctrller.clear(this.scope);
  },
}
</script>

